import { registerApplication, start } from "single-spa";
import {
  constructApplications,
  constructRoutes,
  constructLayoutEngine,
} from "single-spa-layout";
import * as isActive from "./activity-functions";

const routes = constructRoutes(document.querySelector("#single-spa-layout"));
const applications = constructApplications({
  routes,
  loadApp({ name }) {
    return System.import(name);
  },
});
const layoutEngine = constructLayoutEngine({ routes, applications });


/* Registering Micro Frontends --Start-- */
registerApplication(
  "@rohanshukla/home",
  () => System.import("@rohanshukla/home"),
  isActive.home,
  { domElement: document.getElementById('home-container') }
);

registerApplication(
  "@rohanshukla/user-auth",
  () => System.import("@rohanshukla/user-auth"),
  isActive.authUser,
  { domElement: document.getElementById('auth-container') }
);

registerApplication({
  name: "@rohanshukla/dashboard",
  app: () => System.import("@rohanshukla/dashboard"),
  activeWhen: "/dashboard",
  customProps: {
    // domElementGetter: () => document.getElementById('dashboard-container'), // This Also Works
    // domElement: document.getElementById('dashboard-container'),
    value: "This is Custom Props Object..!!..",
    msg: "Custom Props can only be Object"
  },
});
/* Registering Micro Frontends --End-- */


applications.forEach(registerApplication);
layoutEngine.activate();
start();
